@use "../../colors/colors";

.button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  &.primary {
    background: colors.$primary-default;
    color: colors.$neutral-text;
    text-align: center;
    line-height: 24px;
    font-weight: 600;
    font-size: 16px;
    width: 119px;
    height: 52px;
  }
}





